@import"https://fonts.googleapis.com/css2?family=Source+Sans+3:wght@200;300;400;500;600;700;900&display=swap";.car-details__info{display:flex;align-items:center;justify-content:space-around;margin-top:20px;margin-bottom:20px}.car-details__img{flex-basis:50%}.car-details__img img{width:100%}.car-details__desc{flex-basis:50%}.car-details__rating{font-size:20px;display:flex;align-items:center;gap:10px;margin-top:15px}.car-details__rating--outer{position:relative;display:inline-block}.car-details__rating--outer::before{content:"★★★★★";letter-spacing:3px;font-weight:900;color:#7c8a97}.car-details__rating--inner{position:absolute;top:0;left:0;white-space:nowrap;overflow:hidden;width:0}.car-details__rating--inner::before{content:"★★★★★";letter-spacing:3px;font-weight:900;color:#f9a826}.car-details__number-rating{font-size:15px}.car-details span i{color:#f9a826;padding-right:8px}.car-details__feature{display:flex;gap:15px}.car-details__feature span{display:inline-block}.car-details__rent{display:flex;gap:20px;justify-content:space-around}@media only screen and (max-width: 768px){.car-details__info{flex-wrap:wrap;flex-direction:column}.car-details__info h2{text-align:center}.car-details__feature span{font-size:.8rem}.car-details__rent{flex-wrap:wrap;flex-direction:column;align-items:center}}